.left-panel {
  display: flex;
  flex-direction: column;
  align-items: flex-start;  /* Left-align content */
  padding: 50rem 2rem 2rem 2rem; /* Add extra padding at the top */
}

.left-panel h1 {
  margin-top: 4rem; 
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  margin-left: 5rem;
  font-weight: bold;
}

.left-panel h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  margin-left: 5rem;
  color: #aaa;
}

.left-panel p {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1rem;
  margin-left: 5rem;

}

.left-panel .scroll-hint {
  font-size: 0.9rem;
  color: #555;
  margin-top: 1rem;
  font-style: italic;
}

.social-links {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-links a {
  color: #aaa;
  font-size: 1.2rem;
  text-decoration: none;
  transition: color 0.3s;
}

.social-links a:hover {
  color: #1e90ff;
}

.spline-scene-container {
  width: 100%; /* Full width */
  height: 30%; /* Adjust height as needed */
  margin-top: 2rem;
}

.left-panel a {
  color: #888;
  text-decoration: none;
  font-size: 1rem;
  margin: 0.5rem 0;
  transition: color 0.3s;
}

.left-panel a:hover {
  color: #fff;
}

.nav-list {
  list-style: none;
  padding: 0;
  margin-top: 2rem; /* Adjust as needed */
}

.nav-list li {
  display: flex;
  align-items: center;
  margin-top: 1rem; /* Adjust as needed */
  margin-left: 20rem; /* Move the list a little to the right */
}

.nav-list a {
  color: #888;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s, transform 0.3s;
}

.nav-list a:hover {
  color: white;
  transform: scale(1.1); /* Slightly increase size on hover */
}


.nav-list a:hover .line {
  width: 30px; /* Increase line width on hover */
}