@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
}

.app-container {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.left-panel {
  width: 50%;  /* Left panel takes up 50% */
  height: 100vh;
  background-color: #000000;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;  /* Left-align content */
  padding: 2rem;
  font-family: 'Inter', sans-serif;
}

.right-panel {
  width: 50%;  /* Right panel takes up 50% */
  height: 100vh;
  overflow-y: scroll;
  background-color: #000000;
  padding: 2rem;
  color: white;
}

html {
  scroll-behavior: smooth;
}
